import React, { PureComponent } from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  message,
} from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'

@connect(
  state => ({ ...state.printSetUp }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }

  // 获取打印机配置
  componentDidMount() {
    const { handlePrinter, update } = this.props;
    handlePrinter((data) => {
      console.log(data)
      update({ searchParams: { printerIp: data.printerIp, printerName: data.printerName } })
    });
  }

  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }

  query = () => {
    const { searchParams, handlePrintSave } = this.props
    handlePrintSave({ data: { ...searchParams } }, () => {
      message.success('保存成功')
    })
  }


  render() {
    const {
      searchParams: {
        printerIp,
        printerName
      }
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label>当前电脑IP：</label>
              <div className='box'>
                <Input
                  value={printerIp}
                  onChange={(e) => {
                    this.onInputChange({ printerIp: e.target.value })
                  }} />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>打印机名称：</label>
              <div className='box'>
                <Input
                  value={printerName}
                  placeholder="输入打印机名称"
                  onChange={(e) => {
                    this.onInputChange({ printerName: e.target.value })
                  }} />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <Row type="flex" justify="start" >
              <Button style={{ marginLeft: 40 }} type="primary" className={style.mr10} onClick={this.query} >保存</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



