import * as actionType from './actionType'
import {getData} from 'util'
import {API_PRINTER_SAVE, API_PRINTER} from 'api'
//同步
export const update = (object) => ({
  type: actionType.PRINTSETUP_UPDATE,
  data: object
})

//异步

// 保存电脑ip， 打印机名称
export const handlePrintSave = (reqData, callBack) => (dispatch) => {
  dispatch({ type: actionType.PRINTSETUP_UPDATE })
  let api = API_PRINTER_SAVE
  getData({ api, ...reqData }).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PRINTSETUP_FILE
    })
  })
}

// 获取打印机配置
export const handlePrinter = (callBack) => (dispatch) => {
  dispatch({ type: actionType.PRINTSETUP_UPDATE })
  let api = API_PRINTER
  getData({ api }).then(data => {
    callBack && callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.PRINTSETUP_FILE
    })
  })
}




