import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Row,
  Col,
  Card
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.printSetUp}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  handleExport = () => {
    const {handleExport, searchParams, page, size} = this.props
    console.log(searchParams)
    handleExport({searchParams: {page, size, ...searchParams, productName: searchParams.productName ? searchParams.productName.children : ''}}, () => {
      message.success('导出成功')
    })
  }
  render() {
    const blue = {
      color: '#1890ff',
      marginLeft: 20,
      fontSize: 14,
      textAlign: 'center'
    }
    return (
      <div className={style['table-tool']}>
        <Row type="flex" justify="start">
          <Col span={24}>
            <h3 style={{marginLeft: 20, marginTop: 10}}>打印控件下载</h3>
          </Col>
          <Col span={4}>
            <a href="http://www.kdniao.com/file/K-Print_Setup_for_Win64NT_3.020Extend.rar" target="_blank"><Card style={blue} bodyStyle={{padding: 12}} hoverable >64位打印</Card></a>
          </Col>
          <Col span={4}>
            <a href="http://www.kdniao.com/file/K-Print_Setup_for_Win32NT_https_3.020Extend.rar" target="_blank"><Card style={blue} bodyStyle={{padding: 12}} hoverable>32位打印</Card></a>
          </Col>
        </Row>
      </div>
    )
  }
}



